/* MovieScreen.css */
.movieScreen {
  background-color: #000;
  color: #fff;
  padding: 20px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.7);
}

.movieScreenContainer {
  display: flex;
  flex-direction: column;
}

.movieScreenMovieInfo {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.movieScreenMovieBanner {
  width: 100%;
  height: 450px;
  background-size: cover;
  background-position: center center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  box-shadow: 0 -5px 15px rgba(0, 0, 0, 0.7);
}

.movieScreenMovieContent {
  flex: 1;
  padding: 20px;
}

.movieScreenMovieBannerTitle {
  font-size: 28px;
  font-weight: bold;
  margin: 0;
}

.movieScreenMovieBannerDescription {
  font-size: 18px;
  margin-top: 10px;
}

.movieScreen h4 {
  font-size: 20px;
  margin: 20px 0 10px;
}

.movieScreen p {
  font-size: 18px;
}

.movieScreenMovieDropdown {
  margin: 20px 0;
  text-align: center;
}

/* Specific selector for the select element within .movieScreenMovieDropdown */
.movieScreenMovieDropdown select {
  background-color: #222;
  color: #fff;
  padding: 12px;
  border: 2px solid #e50914;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  font-size: 16px;
  font-family: 'Arial', sans-serif;
  width: 100%;
  max-width: 500px;
  background-size: 10px;
  background-position: right 10px center;
  background-repeat: no-repeat;
  margin: 0 auto;
}

/* Specific selector for the select element within .movieScreenMovieDropdown when hovered or focused */
.movieScreenMovieDropdown select:hover,
.movieScreenMovieDropdown select:focus {
  border-color: #e50914;
}

.movieScreenMovieList {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
  justify-content: center;
}

.movieScreenMovie {
  background-color: #222;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.7);
  align-content: center;
  text-align: center;
}

.movieScreenMovieType {
  font-size: 20px;
  padding: 14px;
  margin: 0;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  .movieScreenMovieInfo {
      flex-direction: column;
  }

  .movieScreenMovieContent {
      padding: 20px;
  }

  .movieScreenMovieBannerTitle {
      font-size: 24px;
  }

  .movieScreenMovieBannerDescription {
      font-size: 16px;
  }

  .movieScreen h4 {
      font-size: 18px;
      margin: 20px 0 10px;
  }
}
