.SignInScreen {
    max-width: 300px;
    padding: 70px;
    margin: 0 auto;
    background: rgba(0, 0, 0, 0.85);
    border-radius: 15px;
    transition: all 0.3s ease-in-out;
}
.SignInScreen > form{
    display: grid;
    flex-direction: column;
}
.SignInScreen > form > input {
    outline-width: 0;
    height: 40px;
    margin-bottom: 14px;
    border-radius: 15px;
    border: none;
    padding: 5px 15px;
    transition: all 0.3s ease-in-out;
}
.SignInScreen > form > input:hover,
.SignInScreen > form > input:focus {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.SignInScreen > form > button{
    padding: 16px 20px;
    font-size: 1rem;
    color: #fff;
    border-radius: 5px;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
    margin-top: 20px;
    margin-bottom: 20px;
    transition: all 0.3s ease-in-out;
}
.SignInScreen > form > h1{
    text-align: left;
    margin-bottom: 25px;
}
.SignInScreenGray{
    color: gray;
}
.SignInScreenLink:hover{
    cursor: pointer;
    text-decoration: underline;
}
.SignInScreen > form > button:hover {
    background-color: #f40612;
    transform: scale(1.05);
}