.ProfileScreen{
    height: 100vh;
    color: white;
    /* padding: 20px; */
}

.ProfileScreenBody{
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: 0 auto;
    padding-top: 8%;
    max-width: 800px;
    box-sizing: border-box;
}
.ProfileScreenInfo{
    display: flex;
    margin: 10px 0;
}

.ProfileScreenBody h1{
    font-size: 60px;
    font-weight: 400;
    border-bottom: 1px solid #999;
    margin-bottom: 20px;
    padding-bottom: 10px;
}

.ProfileScreenInfo > img{
    height: 100px;
}

.ProfileScreenDetails{
    color: white;
    margin-left: 25px;
    flex: 1;
}

.ProfileScreenDetails > h2 {
    background-color: gray;
    padding: 15px;
    font-size: 15px;
    padding-left: 20px;
}

.ProfileScreenSignout{
    padding: 10px 20px;
    font-size: 1rem;
    margin-top: 5%;
    width: 100%;
    color: #fff;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
}
.ProfileScreenPlans > h3 {
    border-bottom: 1px solid #282c2d;
    padding-bottom: 10px;

}
.ProfileScreenPlans{
    margin-top: 20px;
}
.PlannsScreenPlan button:hover {
    transform: scale(1.05);
}

.PlannsScreenPlan button:active {
    transform: scale(0.98);
}

.PlannsScreenPlan--disabled button {
    cursor: not-allowed;
}